import statusBar from '../../../components/statusbar/statusbar'
import requestHttps from '../../../common/request.js'
// import navBar from '../../../components/zhouWei-navBar/index.vue'
export default {
	data() {
		return {
			serverImg:getApp().globalData.serverImg,
			bgColorList:'#fff',
			userInfo:[],
			userAuthorization:false,
			spread_url:'11222210001254',
			profit:'',
		}
	},
	methods: {
		// 订单跳转
		goorddetail:function(e){
			var that =this
			var type=e.currentTarget.dataset.type
			console.log(e)
			var title=e.currentTarget.dataset.tit
			var arr=''
			if(title==1){
				arr='淘喵自购订单'
			}else if(title==2){
				arr='拼多多自购订单'
			}else{
				arr='京东自购订单'
			}
			// var title=1
			uni.navigateTo({
				url: '../orderlist/orderlist?type='+type +'&title='+arr,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		goexpect:function(){
			uni.showToast({
				title:'敬请期待',
				icon:'none'
			})
		},
		// 设置
		gomyset:function(){
			uni.navigateTo({
				url:'../myset/myset'
			})
		},
		// 收益统计
		getproift:function(){
			var that=this
			requestHttps({
				url:'/app/profit/stat',
				method:'POST',
				data:{},
				success:res=>{
					console.log(res)
					that.profit=res.data.data
				}
			})
		},
		gocica:function(e){
			var that =this
			var type=e.currentTarget.dataset.type
			console.log(e) 
			uni.navigateTo({
				url: '../cicaorder/cicaorder?type='+type ,
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		// 复制
		paste:function(value) {
			console.log(value)
			uni.setClipboardData({
				data: value
			});
		},
		login:function(){
			uni.navigateTo({
				url:'../login/login'
			})
		},
		// 
		gotosearch:function(e){
			uni.switchTab({
				url:'../../search/search'
			})
		},
		gotofans:function(e){
			uni.navigateTo({
				url:'../fans/fans'
			})
		},
		onNavigationBarButtonTap:function(e){
			console.log(e) 
			if(e.text=="设置"){
				console.log('dddddddd')
			}
		},
		gologin:function(){
			uni.navigateTo({
				url:'../login/login?sin=1'
			})
		},
		
		getuseinfo:function(){
			var that=this
			requestHttps({
				url:'/app/user/info',
				method:'POST',
				data:{},
				success:res=>{
					that.islogin(res.data,1,0)
					console.log(res)
					that.userInfo=res.data.data 
					uni.setStorage({
						key:'userInfo',
						data:res.data.data
					})
				}
			})
		}
	},
	onShow() {
		var that=this
		// var user
		var userAuthorization
		// uni.getStorage({
		// 	key:'userInfo',
		// 	success(e){ 
		// 		console.log(e)
		// 		user=e.data
		// 	}
		// }) 
		uni.getStorage({
			key:'userAuthorization',
			success(e){ 
				console.log(e)
				console.log('1111111111111111111111111e')
				userAuthorization=e.data
			}
		})
		// var user=getApp().globalData.userInfo
		// console.log(getApp().globalData.userInfo)
		that.userAuthorization=userAuthorization
		// that.userInfo=user 
		that.getuseinfo()
		that.getproift()
	},
	onLoad(){
		var that=this
		that.getproift()
	},
	 
}
